}

.mensaje {
  margin-top: 5rem;
}
.mensaje .uptask {
  background: linear-gradient(to right, #F59E0B 0%, #fbd89e 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .mensaje .uptask {
    font-size: 10rem;
  }
}

.confirmar {
  margin-top: 5rem;
}
.confirmar .uptask {
  background: linear-gradient(to right, #4338CA 0%, #b5b1ea 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 7rem;
}
@media (min-width: 768px) {
  .confirmar .uptask {
    font-size: 10rem;
  }
}
.confirmar .acciones {
  justify-content: center;
}
.confirmar .acciones a {
  color: #4338CA;
}

.dashboard {
  background-color: #f3f3f3;
}
@media (min-width: 768px) {
  .dashboard {
    display: flex;
    height: 100vh;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .contenido {
  padding: 2rem;
}
.dashboard .contenido .nombre-pagina {
  font-size: 4rem;
  text-align: center;
}
.dashboard .formulario input:not([type=submit]) {
  background-color: #FFFFFF;
}
.dashboard .formulario input[type=submit] {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  padding: 1rem;
  font-weight: bold;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .dashboard .formulario input[type=submit] {
    width: auto;
  }
}
.dashboard .formulario input[type=submit]:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.dashboard .enlace {
  color: #4338CA;
}
.dashboard .enlace:hover {
  color: #28217b;
}

.sidebar {
  opacity: 0;
  background-color: #4338CA;
  height: 0;
  overflow: hidden;
  transition-property: opacity;
  transition-duration: 500ms;
}
@media (min-width: 768px) {
  .sidebar {
    height: 100%;
    opacity: 1;
    padding: 2rem;
    background-color: #4338CA;
  }
}
.sidebar .contenedor-sidebar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.sidebar .contenedor-sidebar h1 {
  color: #FFFFFF;
  margin: 0;
  font-size: 3rem;
}
.sidebar .contenedor-sidebar .cerrar-menu {
  display: flex;
  justify-content: center;
}
.sidebar .contenedor-sidebar .cerrar-menu:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .contenedor-sidebar .cerrar-menu {
    display: none;
  }
}
.sidebar.mostrar {
  padding: 3rem;
  opacity: 1;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.9);
}
@media (min-width: 768px) {
  .sidebar.mostrar {
    display: none;
  }
}
.sidebar.ocultar {
  opacity: 0;
}
.sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  margin-bottom: 2rem;
  padding: 0.7rem;
  transition-property: background-color;
  transition-duration: 300ms;
  font-size: 2.4rem;
  text-align: center;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.activo {
  background-color: #28217b;
  border-radius: 0.5rem;
  font-weight: bold;
}
@media (min-width: 768px) {
  .sidebar .sidebar-nav a {
    font-size: 1.6rem;
    margin-bottom: 1.2rem;
    text-align: left;
  }
}
.sidebar .cerrar-sesion-mobile {
  margin-top: 4rem;
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile .cerrar-sesion {
    width: auto;
  }
}
.sidebar .cerrar-sesion-mobile .cerrar-sesion:hover {
  background-color: #352ca3;
  cursor: pointer;
}
@media (min-width: 768px) {
  .sidebar .cerrar-sesion-mobile {
    display: none;
  }
}

.barra-mobile {
  background-color: #4338CA;
  padding: 2rem 4rem;
  display: flex;
  justify-content: space-between;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra-mobile {
    display: none;
  }
}
.barra-mobile h1 {
  color: #FFFFFF;
  margin: 0;
}
.barra-mobile .menu img {
  width: 4rem;
}
.barra-mobile .menu img:hover {
  cursor: pointer;
}

.barra {
  display: none;
}
@media (min-width: 768px) {
  .barra {
    background-color: #FFFFFF;
    padding: 1rem 2rem;
    box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.barra p span {
  font-weight: 700;
  color: #4338CA;
}
.barra .cerrar-sesion {
  background-color: #28217b;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .barra .cerrar-sesion {
    width: auto;
  }
}
.barra .cerrar-sesion:hover {
  background-color: #1b1653;
  cursor: pointer;
}

.no-proyecto a {
  color: #4338CA;
}

.listado-proyectos {
  list-style: none;
  padding: 0;
  margin-top: 4rem;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-proyectos .proyecto {
  background-color: #564ccf;
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 1rem;
}
.listado-proyectos .proyecto:hover {
  background-color: #3b31b8;
}
.listado-proyectos .proyecto a {
  color: #FFFFFF;
  cursor: pointer;
  margin: auto;
}

.contenedor-nueva-tarea {
  display: flex;
  justify-content: center;
}
.contenedor-nueva-tarea .agregar-tarea {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .contenedor-nueva-tarea .agregar-tarea {
    width: auto;
  }
}
.contenedor-nueva-tarea .agregar-tarea:hover {
  background-color: #066981;
  cursor: pointer;
}

.listado-tareas {
  list-style: none;
  padding: 0;
  padding-top: 3rem;
}
.listado-tareas .no-tareas {
  text-align: center;
}

.tarea {
  padding: 0.5rem 2rem;
  border-bottom: 2px solid #5f6672;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.tarea:last-of-type {
  border: none;
}
.tarea p {
  font-size: 2rem;
}
.tarea .opciones {
  display: flex;
  gap: 1rem;
}
.tarea .opciones button {
  border: none;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 1rem;
}
.tarea .opciones button:hover {
  cursor: pointer;
}
.tarea .opciones .completado {
  background-color: #2563EB;
  color: #FFFFFF;
}
.tarea .opciones .pendiente {
  background-color: #F59E0B;
  color: #FFFFFF;
}
.tarea .opciones .eliminar-tarea {
  background-color: rgb(169, 0, 0);
  color: #FFFFFF;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .formulario {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-30rem);
  transition-property: all;
  transition-duration: 600ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
  border-radius: 1rem;
}
.modal .formulario input[type=text] {
  border: 1px solid #6b7280;
  border-radius: 0.5rem;
}
.modal .formulario legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .formulario .opciones {
  display: flex;
  justify-content: space-between;
}
.modal .formulario .submit-nueva-tarea {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .submit-nueva-tarea {
    width: auto;
  }
}
.modal .formulario .submit-nueva-tarea:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.modal .formulario .cerrar-modal {
  background-color: #F59E0B;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .modal .formulario .cerrar-modal {
    width: auto;
  }
}
.modal .formulario .cerrar-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.modal .formulario.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .formulario.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}

.filtros {
  background-color: white;
  margin-top: 2rem;
  border-radius: 1rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .filtros .filtros-inputs {
    display: flex;
    align-items: center;
    gap: 3rem;
  }
}
.filtros h2 {
  color: #4338CA;
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .filtros h2 {
    margin: 0;
  }
}
.filtros .campo {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .filtros .campo {
    margin-bottom: 0;
  }
}
.filtros input[type=radio] {
  accent-color: #4338CA;
  width: 1.6rem;
  height: 1.6rem;
}

@media (prefers-color-scheme: dark) {
  body {
    background-color: #2c2c3b;
  }
  .tagline,
.formulario label,
.login .acciones a,
.crear .acciones a,
.olvide .acciones a {
    color: #FFFFFF;
  }
  .descripcion-pagina,
.formulario .campo input {
    color: #f3f3f3;
  }
  .alerta.error {
    background-color: #FFFFFF;
  }
  .formulario .campo input {
    background-color: #585875;
  }
}